import React, { useState } from "react";
import { connect } from "react-redux";
import {
  MdAdd,
  MdClear,
  MdCreate,
  MdDelete,
  MdExpandLess,
  MdExpandMore,
} from "react-icons/md";
import {
  FC_CreateCenter,
  FC_CreateRoom,
  FC_DeleteCenter,
  FC_DeleteRoom,
  FC_UpdateCenter,
  FC_UpdateRoom,
} from "../../actions/location";
import axios from "axios";
import { API } from "../../utils/api";
import {
  CentersInterface,
  DistrictInterface,
  RoomLocationInterface,
  RoomsInterface,
} from "../../shared/interfaces";
import { PushPopupAlert } from "../../actions/system";
import { PopupAlertData } from "../../components/Alert/Popup";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { errorToText } from "../../utils/errors";
import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";
import { AiOutlineFileSearch } from "react-icons/ai";
import RoomForm from "./RoomForm";
import CenterForm from "./CenterForm";
import searchData from "../../utils/search";
import { isEmptyOrSpaces } from "../../shared/dataTest";
import ActionConfirm, {
  ActionConfirmEnum,
} from "../../components/ActionConfirm/ActionConfirm";

interface AppProps {
  thisProvinceId: string;
  thisDistrict: DistrictInterface | null;
  thisCenter: CentersInterface | null;
  thisRoom: RoomsInterface | null;
  thisProvince: RoomLocationInterface | null;
  locations: RoomLocationInterface[];
  loadingRequest: boolean;
  actionTypes:
    | ""
    | "CREATE_PROVINCE"
    | "UPDATE_PROVINCE"
    | "CREATE_DISTRICT"
    | "UPDATE_DISTRICT"
    | "CREATE_CENTER"
    | "UPDATE_CENTER"
    | "CREATE_ROOM"
    | "UPDATE_ROOM";
  setLoadingRequest: React.Dispatch<React.SetStateAction<boolean>>;
  setThisCenter: React.Dispatch<React.SetStateAction<CentersInterface | null>>;
  setThisRoom: React.Dispatch<React.SetStateAction<RoomsInterface | null>>;
  setThisDistrict: React.Dispatch<
    React.SetStateAction<DistrictInterface | null>
  >;
  setThisProvince: React.Dispatch<
    React.SetStateAction<RoomLocationInterface | null>
  >;
  setActionTypes: React.Dispatch<
    React.SetStateAction<
      | ""
      | "CREATE_PROVINCE"
      | "UPDATE_PROVINCE"
      | "CREATE_DISTRICT"
      | "UPDATE_DISTRICT"
      | "CREATE_CENTER"
      | "UPDATE_CENTER"
      | "CREATE_ROOM"
      | "UPDATE_ROOM"
    >
  >;
  FC_CreateCenter: Function;
  FC_UpdateCenter: Function;
  FC_DeleteCenter: Function;
  FC_CreateRoom: Function;
  FC_UpdateRoom: Function;
  FC_DeleteRoom: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

const ViewCenters: React.FC<AppProps> = (props) => {
  const [centerName, setCenterName] = useState<string>("");
  const [deleteCenter, setDeleteCenter] = useState<string>("");
  const [deleteRoom, setDeleteRoom] = useState<string>("");
  const [roomNumber, setRoomNumber] = useState<number>(0);
  const [roomCapacity, setRoomCapacity] = useState<number>(0);
  const [error, setError] = useState<{ element: string; msg: string } | null>(
    null
  );
  const [loadingDeletion, setLoadingDeletion] = useState<string>("");
  const [searchCenter, setSearchCenter] = useState<string>("");

  const handleSubmit = async () => {
    if (!props.thisProvince) {
      return setError({
        element: "province_id",
        msg: "Province is required",
      });
    }
    if (!props.thisDistrict) {
      return setError({
        element: "district_id",
        msg: "District is required",
      });
    }
    if (!centerName.length) {
      return setError({
        element: "center_name",
        msg: "Center name is required",
      });
    }
    props.setLoadingRequest(true);
    try {
      let res;
      if (props.actionTypes === "CREATE_CENTER") {
        res = await axios.post<{ data: CentersInterface }>(
          `${API.APPLICATION}/center`,
          {
            district_id: props.thisDistrict._id,
            center_name: centerName,
            rooms: [],
          }
        );
        if (res.data) {
          props.FC_CreateCenter({
            province_id: props.thisProvinceId,
            district_id: props.thisDistrict._id,
            data: res.data.data,
          });
          props.PushPopupAlert({
            title: "New Center created successfully",
            theme: PopupAlertTheme.success,
          });
          props.setLoadingRequest(false);
          props.setActionTypes("");
          setCenterName("");
        }
      } else if (props.thisCenter && props.actionTypes === "UPDATE_CENTER") {
        const rooms: string[] = props.thisCenter.rooms.map((room) => room._id);
        res = await axios.patch<{ update: CentersInterface }>(
          `${API.APPLICATION}/center`,
          {
            examination_center_id: props.thisCenter._id,
            center_name: centerName,
            rooms,
            isUsed: true,
          }
        );
        if (res.data) {
          props.FC_UpdateCenter({
            province_id: props.thisProvinceId,
            district_id: props.thisDistrict._id,
            examination_center_id: props.thisCenter._id,
            center_name: centerName,
          });
          props.PushPopupAlert({
            title: "Center updated successfully",
            theme: PopupAlertTheme.success,
          });
          props.setLoadingRequest(false);
          props.setActionTypes("");
          setCenterName("");
        }
      }
    } catch (error) {
      console.log({ doc_category_error: error });
      props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
      props.setLoadingRequest(false);
    }
  };

  const handleDelete = async (centerId: string) => {
    if (isEmptyOrSpaces(props.thisProvinceId)) {
      return PushPopupAlert({
        title: "Province is required!",
        theme: PopupAlertTheme.warning,
      });
    }
    if (!props.thisDistrict) {
      return PushPopupAlert({
        title: "District is required!",
        theme: PopupAlertTheme.warning,
      });
    }
    setLoadingDeletion(centerId);
    try {
      const res = await axios.delete<CentersInterface>(
        `${API.APPLICATION}/center/${centerId}`
      );

      if (res.data) {
        props.FC_DeleteCenter({
          province_id: props.thisProvinceId,
          district_id: props.thisDistrict._id,
          center_id: centerId,
        });
        let newArr: DistrictInterface = { ...props.thisDistrict }; // copying the old datas array
        let center: CentersInterface[] = [];

        center = props.thisDistrict.centers.filter(
          (element) => element._id !== centerId
        );
        newArr.centers = center;
        props.setThisDistrict(newArr);

        props.PushPopupAlert({
          title: "Center deleted successfully",
          theme: PopupAlertTheme.success,
        });
        props.setLoadingRequest(false);
        props.setActionTypes("");
      }
    } catch (error) {
      console.log({ province_error: error });
      props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
      props.setLoadingRequest(false);
    }
    setLoadingDeletion("");
  };

  const handleSubmitRoom = async () => {
    if (!props.thisProvince) {
      return setError({
        element: "province_id",
        msg: "Province is required",
      });
    }

    if (!props.thisDistrict) {
      return setError({
        element: "district_id",
        msg: "District is required",
      });
    }

    if (!props.thisCenter) {
      return setError({
        element: "center_id",
        msg: "Center is required",
      });
    }

    if (roomNumber <= 0) {
      return setError({
        element: "room_number",
        msg: "Room number is required",
      });
    }

    if (roomCapacity <= 0) {
      return setError({
        element: "room_capacity",
        msg: "Enter valid room capacity",
      });
    }
    props.setLoadingRequest(true);
    try {
      let res;
      if (props.actionTypes === "CREATE_ROOM") {
        res = await axios.post<{
          data: RoomsInterface;
        }>(`${API.APPLICATION}/centerroom`, {
          center_id: props.thisCenter._id,
          room_type: "Physical room",
          room_number: roomNumber,
          capacity: roomCapacity,
          occupied: 0,
        });
        if (res.data) {
          props.FC_CreateRoom({
            province_id: props.thisProvinceId,
            district_id: props.thisDistrict._id,
            center_id: props.thisCenter._id,
            data: res.data.data,
          });
          props.PushPopupAlert({
            title: "New Room created successfully",
            theme: PopupAlertTheme.success,
          });
          props.setLoadingRequest(false);
          props.setActionTypes("");
          setRoomNumber(0);
          setRoomCapacity(0);
        }
      } else if (props.thisRoom && props.actionTypes === "UPDATE_ROOM") {
        res = await axios.patch<{ data: RoomsInterface }>(
          `${API.APPLICATION}/centerroom`,
          {
            examination_center_room_id: props.thisRoom._id,
            center_id: props.thisCenter._id,
            room_type: props.thisRoom.room_type,
            room_number: roomNumber,
            capacity: roomCapacity,
            occupied: props.thisRoom.occupied,
            isUsed: true,
          }
        );
        if (res.data) {
          props.FC_UpdateRoom({
            province_id: props.thisProvinceId,
            district_id: props.thisDistrict._id,
            center_id: props.thisCenter._id,
            room_id: props.thisRoom._id,
            room_number: roomNumber,
            capacity: roomCapacity,
          });
          props.PushPopupAlert({
            title: "Room updated successfully",
            theme: PopupAlertTheme.success,
          });
          props.setLoadingRequest(false);
          props.setActionTypes("");
          setRoomNumber(0);
          setRoomCapacity(0);
          props.setThisRoom(null);
        }
      }
    } catch (error) {
      console.log({ doc_category_error: error });
      props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
      props.setLoadingRequest(false);
    }
  };

  const handleDeleteRoom = async (roomId: string) => {
    if (isEmptyOrSpaces(props.thisProvinceId)) {
      return PushPopupAlert({
        title: "Province is required!",
        theme: PopupAlertTheme.warning,
      });
    }
    if (!props.thisDistrict) {
      return PushPopupAlert({
        title: "District is required!",
        theme: PopupAlertTheme.warning,
      });
    }
    if (props.thisCenter === null) {
      return PushPopupAlert({
        title: "Center is required!",
        theme: PopupAlertTheme.warning,
      });
    }
    setLoadingDeletion(roomId);
    try {
      const res = await axios.delete<CentersInterface>(
        `${API.APPLICATION}/centerroom/${roomId}`
      );
      if (res.data) {
        console.log({ res });
        let newArr: CentersInterface = { ...props.thisCenter }; // copying the old datas array
        let room: RoomsInterface[] = [];

        room = props.thisCenter.rooms.filter(
          (element) => element._id !== roomId
        );
        newArr.rooms = room;
        props.FC_DeleteRoom({
          province_id: props.thisProvinceId,
          district_id: props.thisDistrict._id,
          center_id: props.thisCenter._id,
          room_id: roomId,
        });
        props.setThisCenter(newArr);
        props.PushPopupAlert({
          title: "Room deleted successfully",
          theme: PopupAlertTheme.success,
        });
        props.setLoadingRequest(false);
        props.setActionTypes("");
      }
    } catch (error) {
      console.log({ province_error: error });
      props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
      props.setLoadingRequest(false);
    }
    setLoadingDeletion("");
  };

  const selectProvince = (province_id: string | "ALL") => {
    if (province_id !== "ALL") {
      let province = props.locations.find((items) => items._id === province_id);
      if (province) {
        props.setThisProvince(province);
        if (province.districts.length > 0) {
          props.setThisDistrict(null);
        }
      }
    } else {
      props.setThisProvince(null);
      props.setThisDistrict(null);
    }
  };

  const selectDistrict = (province_id: string, district_id: string) => {
    if (province_id !== "ALL" || !isEmptyOrSpaces(province_id)) {
      const province = props.locations.find(
        (items) => items._id === province_id
      );
      if (province) {
        const district = province.districts.find((d) => d._id === district_id);
        props.setThisDistrict(district ? district : null);
      }
      props.setThisCenter(null);
    }
  };

  const selectCenter = (
    province_id: string,
    district_id: string,
    center_id: string
  ) => {
    if (
      province_id !== "ALL" ||
      !isEmptyOrSpaces(province_id) ||
      district_id !== "ALL" ||
      !isEmptyOrSpaces(district_id)
    ) {
      const province = props.locations.find(
        (items) => items._id === province_id
      );
      if (province) {
        const district = province.districts.find((d) => d._id === district_id);
        props.setThisDistrict(district ? district : null);
      }
      props.setThisCenter(null);
    }
    props.setThisCenter(
      props.locations
        .filter((items) => items._id === province_id)[0]
        .districts.filter((d) => d._id === district_id)[0]
        .centers.filter((item) => item._id === center_id)[0]
    );
  };

  const isRoomExist = (thisCenter: CentersInterface): boolean => {
    if (thisCenter.rooms.length > 0) {
      return true;
    }
    return false;
  };

  const cleanModal = () => {
    if (
      props.actionTypes === "CREATE_ROOM" ||
      props.actionTypes === "UPDATE_ROOM"
    ) {
      props.setThisRoom(null);
      setRoomCapacity(0);
      setRoomNumber(0);
    }
    if (
      (props.actionTypes === "CREATE_CENTER" ||
        props.actionTypes === "UPDATE_CENTER") &&
      props.thisProvince
    ) {
      setCenterName("");
    }
    props.setActionTypes("");
    setError(null);
  };

  const getDistinctCenter = (
    province: RoomLocationInterface | null,
    district: DistrictInterface | null
  ): CentersInterface[] => {
    let thisProvince: RoomLocationInterface | undefined = undefined;
    let thisDistrict: DistrictInterface | undefined = undefined;
    let result: CentersInterface[] = [];
    if (province && !district) {
      thisProvince = props.locations.find(
        (item) =>
          item.province_code.toString() === province.province_code.toString()
      );
      if (thisProvince) {
        for (const key in thisProvince.districts) {
          if (
            Object.prototype.hasOwnProperty.call(thisProvince.districts, key)
          ) {
            const elementDistrict = thisProvince.districts[key];
            for (const key in elementDistrict.centers) {
              if (
                Object.prototype.hasOwnProperty.call(
                  elementDistrict.centers,
                  key
                )
              ) {
                result.push(elementDistrict.centers[key]);
              }
            }
          }
        }
      }
    }

    if (province && district) {
      thisProvince = props.locations.find(
        (item) =>
          item.province_code.toString() === province.province_code.toString()
      );
      if (thisProvince) {
        thisDistrict = thisProvince.districts.find(
          (item) =>
            item.district_code.toString() === district.district_code.toString()
        );
        if (thisDistrict) {
          for (const key in thisDistrict.centers) {
            if (
              Object.prototype.hasOwnProperty.call(thisDistrict.centers, key)
            ) {
              result.push(thisDistrict.centers[key]);
            }
          }
        }
      }
    }

    if (!province && !district) {
      for (const provKey in props.locations) {
        if (Object.prototype.hasOwnProperty.call(props.locations, provKey)) {
          const elementProv = props.locations[provKey];
          for (const distKey in elementProv.districts) {
            if (
              Object.prototype.hasOwnProperty.call(
                elementProv.districts,
                distKey
              )
            ) {
              const elementDistrict = elementProv.districts[distKey];
              for (const key in elementDistrict.centers) {
                if (
                  Object.prototype.hasOwnProperty.call(
                    elementDistrict.centers,
                    key
                  )
                ) {
                  result.push(elementDistrict.centers[key]);
                }
              }
            }
          }
        }
      }
    }
    return result;
  };

  const selectLocation = (district: string) => {
    let thisProvince: RoomLocationInterface | undefined = undefined;
    let thisDistrict: DistrictInterface | undefined = undefined;
    let result: CentersInterface[] = [];

    for (const provKey in props.locations) {
      if (Object.prototype.hasOwnProperty.call(props.locations, provKey)) {
        thisDistrict = props.locations[provKey].districts.find(
          (item) => item._id === district
        );
        if (thisDistrict) {
          const _thisDistrict = thisDistrict;
          thisProvince = props.locations.find(
            (item) =>
              item.province_code.toString() ===
              _thisDistrict.province_code.toString()
          );

          if (thisProvince) {
            props.setThisProvince(thisProvince);
            props.setThisDistrict(thisDistrict);
          }
        }
      }
    }
    return result;
  };

  return (
    <div
      className={`bg-white w-full p-2 border border-gray-300 rounded shadow`}
    >
      <div className="border-b px-2 py-3 flex justify-between">
        <h2 className="text-3xl font-semibold text-gray-800">
          List of Centers
        </h2>
        <div className="flex items-center gap-2">
          <button
            type="button"
            className={`btn mb-1 border-2 border-blue-400 bg-blue-400 hover:bg-blue-500 hover:border-blue-500
        text-white rounded-md flex items-center font-bold focus:outline-none ${
          props.loadingRequest && "cursor-wait"
        } ${props.actionTypes !== "" && "cursor-not-allowed"}`}
            onClick={() => props.setActionTypes("CREATE_CENTER")}
            disabled={props.actionTypes !== "" || props.loadingRequest}
          >
            <MdAdd className="inline-block mr-1 text-xl font-bold" /> New Center
          </button>
          <button
            type="button"
            className={`btn hover:bg-blue-500 mb-1 border-2 border-blue-500 bg-white
        hover:text-white rounded-md flex items-center font-bold text-gray-700 focus:outline-none gap-1 ${
          props.loadingRequest && "cursor-wait"
        } ${props.actionTypes !== "" && "cursor-not-allowed"}`}
            onClick={() => props.setActionTypes("CREATE_ROOM")}
            disabled={props.actionTypes !== "" || props.loadingRequest}
          >
            <MdAdd className="inline-block mr-1 text-xl font-bold" />
            New Room
          </button>
        </div>
      </div>

      {/* FILTER FORM ======================================== */}
      <div className="flex flex-col sm:flex-row items-center gap-2 w-full my-2">
        <div className="w-full sm:w-1/5">
          <select
            className="input-white h-10 text-sm border-2 border-gray-400 focus:border-gray-500 w-full"
            placeholder={`Filter by province`}
            onChange={(e: React.ChangeEvent<HTMLSelectElement>) =>
              selectProvince(e.target.value)
            }
            value={props.thisProvince ? props.thisProvince._id : ""}
          >
            <option value="ALL">All province</option>
            {props.locations.map((province, index) => (
              <option value={province._id} key={index + province._id}>
                {province.province_name}
              </option>
            ))}
          </select>
        </div>
        <div className="w-full sm:w-1/5">
          <select
            className="input-white h-10 text-sm border-2 border-gray-400 focus:border-gray-500 w-full"
            placeholder={`Filter by district`}
            onChange={(e: React.ChangeEvent<HTMLSelectElement>) =>
              selectDistrict(props.thisProvince!._id, e.target.value)
            }
            value={props.thisDistrict ? props.thisDistrict._id : "ALL"}
          >
            <option value="ALL">All district</option>
            {props.thisProvince !== null ? (
              props.locations.filter(
                (loc) => loc._id === props.thisProvince!._id
              ).length > 0 ? (
                props.locations
                  .filter((loc) => loc._id === props.thisProvince!._id)[0]
                  .districts.map((district, index) => (
                    <option value={district._id} key={index + district._id}>
                      {district.district_name}
                    </option>
                  ))
              ) : (
                <option value="" disabled>
                  No district found
                </option>
              )
            ) : (
              <option value="" disabled>
                Select province first!
              </option>
            )}
          </select>
        </div>
        <div className="w-full sm:w-3/5">
          <input
            onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
              setSearchCenter(e.target.value)
            }
            type="text"
            className="input-white h-10 text-sm border-2 border-gray-400 focus:border-gray-500"
            placeholder={`Search centers by title`}
            value={searchCenter}
            disabled={
              getDistinctCenter(props.thisProvince, props.thisDistrict).length >
              0
                ? false
                : true
            }
          />
        </div>
      </div>
      {/* END FILTER FORM ======================================== */}

      {getDistinctCenter(props.thisProvince, props.thisDistrict).length > 0 ? (
        <div className="bg-white">
          {searchData(
            getDistinctCenter(props.thisProvince, props.thisDistrict),
            searchCenter
          ).map((center: CentersInterface, index) => (
            <div
              key={index + center._id}
              className="p-2 my-3 rounded border hover:bg-gray-50"
            >
              <div
                className="flex justify-between items-center group cursor-pointer"
                onClick={() =>
                  !props.thisCenter || props.thisCenter._id !== center._id
                    ? props.setThisCenter(center)
                    : props.setThisCenter(null)
                }
              >
                <div className="ml-2">
                  <div className="text-2xl font-semibold text-gray-600">
                    {center.center_name}
                  </div>
                  <div className="text-base top-1 text-gray-600">
                    {center.rooms.length > 0
                      ? center.rooms.length + " Room(s)"
                      : null}
                  </div>
                </div>
                <div className="flex items-center gap-2">
                  {props.thisCenter && props.thisCenter._id === center._id && (
                    <button
                      className={`text-white bg-blue-400 group-hover:bg-blue-600 focus:outline-none p-2 rounded-full group-hover:shadow-md group-hover:text-white  ${
                        props.loadingRequest && "cursor-wait"
                      } ${props.actionTypes !== "" && "cursor-not-allowed"}`}
                      onClick={() => {
                        props.setThisCenter(center);
                        setCenterName(center.center_name);
                        selectLocation(center.district_id);
                        props.setActionTypes("UPDATE_CENTER");
                      }}
                      disabled={
                        props.loadingRequest || props.actionTypes !== ""
                      }
                      title="Edit center"
                    >
                      <MdCreate className="w-6 h-6" />
                    </button>
                  )}
                  {props.thisCenter &&
                    props.thisCenter._id === center._id &&
                    !isRoomExist(props.thisCenter) && (
                      <button
                        title="Delete center"
                        className={`bg-red-400 group-hover:bg-red-600 text-white focus:outline-none p-2 rounded-full group-hover:shadow-md group-hover:text-white ${
                          props.loadingRequest && "cursor-wait"
                        } ${props.actionTypes !== "" && "cursor-not-allowed"}`}
                        onClick={() => setDeleteCenter(center._id)}
                        disabled={
                          props.loadingRequest ||
                          loadingDeletion.length > 0 ||
                          props.actionTypes !== ""
                        }
                      >
                        <MdClear className="w-6 h-6" />
                      </button>
                    )}

                  <button
                    title={
                      props.thisCenter && props.thisCenter._id === center._id
                        ? "Close Center"
                        : "View Rooms"
                    }
                    className={`${
                      props.thisCenter && props.thisCenter._id === center._id
                        ? "group-hover:bg-gray-600"
                        : "group-hover:bg-gray-500"
                    } p-2 rounded-full bg-gray-300 group-hover:shadow-md flex justify-center items-center focus:outline-none text-gray-800 group-hover:text-white`}
                    onClick={() =>
                      !props.thisCenter || props.thisCenter._id !== center._id
                        ? props.setThisCenter(center)
                        : props.setThisCenter(null)
                    }
                  >
                    {props.thisCenter && props.thisCenter._id === center._id ? (
                      <MdExpandLess className="w-6 h-6" />
                    ) : (
                      <MdExpandMore className="w-6 h-6" />
                    )}
                  </button>
                </div>
              </div>

              {props.thisCenter && props.thisCenter._id === center._id && (
                <div
                  className={`bg-white h-full w-full mt-2 p-2 pb-5 border border-gray-300 rounded shadow animate__animated animate__fadeIn`}
                >
                  <div className="border-b px-2 py-3 flex justify-between">
                    <h2 className="text-3xl font-semibold text-gray-800">
                      List of Rooms
                    </h2>

                    <button
                      type="button"
                      className={`btn hover:bg-blue-500 mb-1 border-2 border-blue-500 bg-white hover:text-white rounded-md flex items-center font-bold text-gray-700 focus:outline-none gap-1 ${
                        props.loadingRequest && "cursor-wait"
                      } ${props.actionTypes !== "" && "cursor-not-allowed"}`}
                      onClick={() => {
                        props.setActionTypes("CREATE_ROOM");
                        selectLocation(center.district_id);
                      }}
                      disabled={
                        props.actionTypes !== "" || props.loadingRequest
                      }
                    >
                      <MdAdd className="inline-block mr-1 text-xl font-bold" />
                      New Room
                    </button>
                  </div>

                  {center.rooms.length > 0 ? (
                    <table
                      className={`w-full shadow sm:rounded-lg table-auto order-table max-w-full`}
                    >
                      <thead className="bg-white border-b-2 border-gray-300 text-gray-600 font-bold text-sm">
                        <tr
                          className={`text-left tracking-wide bg-gray-100 border-b-2 border-gray-500 text-gray-600 overflow-hidden`}
                        >
                          <th className={`p-3 text-left`}>Room</th>
                          <th className={`p-3 text-left`}>Type</th>
                          <th className={`p-3 text-left`}>Capacity</th>
                          {/* <th className={`p-3 text-left`}>Occupied</th> */}
                          <th className={`p-3 text-left`}></th>
                        </tr>
                      </thead>
                      <tbody
                        id={`table-data_${center._id}`}
                        className="bg-white text-black text-base"
                      >
                        {center.rooms.map(
                          (room: RoomsInterface, index: number) => (
                            <tr key={index + room._id}>
                              {/* <td className="p-3 text-left whitespace-nowrap">{index + 1}</td> */}
                              <td className="p-3 text-left">
                                {room.room_number}
                              </td>
                              <td className="p-3 text-left">
                                {room.room_type}
                              </td>
                              <td className="p-3 text-left">{room.capacity}</td>
                              {/* <td className="p-3 text-left">{room.occupied}</td> */}
                              <td>
                                <div className="flex items-center justify-center gap-2">
                                  <button
                                    title="Update this field"
                                    className="bg-gray-50 hover:bg-blue-100 rounded-full px-1 py-1 focus:outline-none"
                                    onClick={() => {
                                      props.setThisRoom(room);
                                      setRoomCapacity(room.capacity);
                                      setRoomNumber(room.room_number);
                                      selectLocation(center.district_id);
                                      props.setActionTypes("UPDATE_ROOM");
                                    }}
                                    disabled={
                                      props.loadingRequest ||
                                      props.actionTypes !== ""
                                    }
                                  >
                                    <MdCreate className="text-blue-700 w-7 h-7" />
                                  </button>
                                  <button
                                    title="Delete this field"
                                    className="bg-gray-50 hover:bg-blue-100 rounded-full px-1 py-1 focus:outline-none"
                                    onClick={() => setDeleteRoom(room._id)}
                                    disabled={
                                      props.loadingRequest ||
                                      props.actionTypes !== ""
                                    }
                                  >
                                    {loadingDeletion === room._id ? (
                                      "Loading..."
                                    ) : (
                                      <div className="flex justify-center items-center gap-1">
                                        <MdDelete className="text-red-700 w-7 h-7" />
                                      </div>
                                    )}
                                  </button>
                                </div>
                              </td>
                            </tr>
                          )
                        )}
                      </tbody>
                    </table>
                  ) : (
                    <div className="text-center py-8">
                      <AiOutlineFileSearch className="w-24 h-24 mx-auto text-gray-600" />
                      <span className="text-gray-600 text-2xl">
                        No Data Found
                      </span>
                    </div>
                  )}
                </div>
              )}
            </div>
          ))}
        </div>
      ) : (
        <div className="text-center py-8">
          <AiOutlineFileSearch className="w-24 h-24 mx-auto text-gray-600" />
          <span className="text-gray-600 text-2xl">No Data Found</span>
        </div>
      )}

      {/* modal for creating new categories or sub category */}
      {props.actionTypes !== "" && (
        <Modal
          backDrop={true}
          theme={Themes.default}
          close={(): void => cleanModal()}
          displayClose={true}
          backDropClose={false}
          title={
            <div className="flex items-center space-x-5">
              <div className="block pl-2 font-semibold text-xl self-start text-gray-700">
                <h2 className="uppercase text-back">
                  {props.actionTypes === "CREATE_ROOM"
                    ? "CREATING NEW ROOM"
                    : props.actionTypes === "UPDATE_ROOM" &&
                      "UPDATING EXISTING ROOM"}
                  {props.actionTypes === "CREATE_CENTER"
                    ? "CREATING NEW CENTER"
                    : props.actionTypes === "UPDATE_CENTER" &&
                      "UPDATING EXISTING CENTER"}
                </h2>
              </div>
            </div>
          }
          widthSizeClass={ModalSize.medium}
          padding={{ title: true, body: false, footer: false }}
        >
          <div className="mx-5 mb-8">
            <div className="divide-y divide-gray-200">
              {(props.actionTypes === "CREATE_ROOM" ||
                props.actionTypes === "UPDATE_ROOM") && (
                <RoomForm
                  roomNumber={roomNumber}
                  roomCapacity={roomCapacity}
                  thisRoom={props.thisRoom}
                  setError={setError}
                  loadingRequest={props.loadingRequest}
                  setRoomNumber={setRoomNumber}
                  setRoomCapacity={setRoomCapacity}
                  setThisRoom={props.setThisRoom}
                  setActionTypes={props.setActionTypes}
                  handleSubmitRoom={handleSubmitRoom}
                  thisDistrict={props.thisDistrict}
                  thisProvince={props.thisProvince}
                  locations={props.locations}
                  error={error}
                  selectProvince={selectProvince}
                  selectDistrict={selectDistrict}
                  selectCenter={selectCenter}
                  thisCenter={props.thisCenter}
                />
              )}
              {(props.actionTypes === "CREATE_CENTER" ||
                props.actionTypes === "UPDATE_CENTER") && (
                <CenterForm
                  centerName={centerName}
                  error={error}
                  loadingRequest={props.loadingRequest}
                  setCenterName={setCenterName}
                  setError={setError}
                  setActionTypes={props.setActionTypes}
                  handleSubmit={handleSubmit}
                  thisDistrict={props.thisDistrict}
                  thisProvince={props.thisProvince}
                  locations={props.locations}
                  selectProvince={selectProvince}
                  selectDistrict={selectDistrict}
                />
              )}
            </div>
          </div>
        </Modal>
      )}
      {/* end modal for creating new categories or sub category */}

      {/* ----------------------------------------------------------- */}
      {!isEmptyOrSpaces(deleteCenter) && (
        <ActionConfirm
          modalType={ActionConfirmEnum.DELETE}
          confirmFun={() => handleDelete(deleteCenter)}
          setModalAction={(status: boolean) => !status && setDeleteCenter("")}
          description={
            "Do you really want to delete this center? This process can affect application shortlisting"
          }
        />
      )}
      {!isEmptyOrSpaces(deleteRoom) && (
        <ActionConfirm
          modalType={ActionConfirmEnum.DELETE}
          confirmFun={() => handleDeleteRoom(deleteRoom)}
          setModalAction={(status: boolean) => !status && setDeleteRoom("")}
          description={
            "Do you really want to delete this room? This process can affect application shortlisting"
          }
        />
      )}
    </div>
  );
};

export default React.memo(connect(null, {
  FC_CreateCenter,
  FC_UpdateCenter,
  FC_DeleteCenter,
  FC_CreateRoom,
  FC_UpdateRoom,
  FC_DeleteRoom,
  PushPopupAlert,
})(ViewCenters));
