import React, { Fragment } from "react";
import { MdCancel, MdSave } from "react-icons/md";
import {
  DistrictInterface,
  RoomLocationInterface,
} from "../../shared/interfaces";

interface CenterFormProps {
  thisDistrict: DistrictInterface | null;
  thisProvince: RoomLocationInterface | null;
  locations: RoomLocationInterface[];
  loadingRequest: boolean;
  centerName: string;
  error: {
    element: string;
    msg: string;
  } | null;
  setCenterName: (value: React.SetStateAction<string>) => void;
  setError: (
    value: React.SetStateAction<{
      element: string;
      msg: string;
    } | null>
  ) => void;
  setActionTypes: React.Dispatch<
    React.SetStateAction<
      | ""
      | "CREATE_PROVINCE"
      | "UPDATE_PROVINCE"
      | "CREATE_DISTRICT"
      | "UPDATE_DISTRICT"
      | "CREATE_CENTER"
      | "UPDATE_CENTER"
      | "CREATE_ROOM"
      | "UPDATE_ROOM"
    >
  >;
  handleSubmit: () => Promise<void>;
  selectProvince: (province_id: string) => void;
  selectDistrict: (province_id: string, district_id: string) => void;
}

const CenterForm: React.FC<CenterFormProps> = (props) => {
  return (
    <Fragment>
      <div className="py-4 text-base leading-6 space-y-4 text-gray-700 sm:text-lg sm:leading-7">
        {/* PROVINCE SELECT ========================================== */}
        <div className="flex flex-col">
          <label>Province</label>
          <select
            className={`bg-white py-1 px-2 border-2 w-full focus:outline-none focus:bg-gray-100 text-gray-600 focus:text-gray-800 rounded h-10 md:w-full ${
              props.error && props.error.element === "province_id"
                ? "border-red-700"
                : "border-gray-400 focus:border-gray-500"
            }`}
            onChange={(e) => {
              props.error &&
                props.error.element === "province_id" &&
                props.setError(null);
              props.selectProvince(e.target.value);
            }}
            value={props.thisProvince ? props.thisProvince._id : ""}
            disabled={props.loadingRequest}
          >
            <option value="" disabled>
              Select province
            </option>
            {props.locations.map((item, index) => (
              <option value={item._id} key={item._id + index}>
                {item.province_name}
              </option>
            ))}
          </select>
          {props.error && (
            <span className="text-red-700">{props.error.msg}</span>
          )}
        </div>
        {/* END PROVINCE SELECT ========================================== */}

        {/* DISTRICT SELECT ========================================== */}
        <div className="flex flex-col">
          <label>District</label>
          <select
            className={`bg-white py-1 px-2 border-2 w-full focus:outline-none focus:bg-gray-100 text-gray-600 focus:text-gray-800 rounded h-10 md:w-full ${
              props.error && props.error.element === "district_id"
                ? "border-red-700"
                : "border-gray-400 focus:border-gray-500"
            } ${!props.thisProvince && "cursor-not-allowed"}`}
            onChange={(e) => {
              props.error &&
                props.error.element === "district_id" &&
                props.setError(null);
              props.selectDistrict(
                props.thisProvince ? props.thisProvince._id : "",
                e.target.value
              );
            }}
            value={props.thisDistrict ? props.thisDistrict._id : ""}
            disabled={props.loadingRequest || props.thisProvince === null}
          >
            <option value="" disabled>
              Select district
            </option>
            {props.thisProvince !== null &&
            props.locations.filter((loc) => loc._id === props.thisProvince!._id)
              .length > 0 ? (
              props.locations
                .filter((loc) => loc._id === props.thisProvince!._id)[0]
                .districts.map((district, index) => (
                  <option value={district._id} key={index + district._id}>
                    {district.district_name}
                  </option>
                ))
            ) : (
              <option value="" disabled>
                No district found
              </option>
            )}
          </select>
          {props.error && (
            <span className="text-red-700">{props.error.msg}</span>
          )}
        </div>
        {/* END DISTRICT SELECT ========================================== */}

        <div className="flex flex-col">
          <label>Center name</label>
          <input
            type="text"
            className={`bg-white py-1 px-2 border-2 ${
              props.error && props.error.element === "center_name"
                ? "border-red-700"
                : "border-gray-400 focus:border-gray-500"
            } rounded focus:outline-none focus:bg-gray-100`}
            onChange={(e) => {
              props.error &&
                props.error.element === "center_name" &&
                props.setError(null);
              props.setCenterName(e.target.value);
            }}
            value={props.centerName}
            disabled={props.loadingRequest}
          />
          {props.error && (
            <span className="text-red-700">{props.error.msg}</span>
          )}
        </div>
      </div>
      <div className="pt-4 flex justify-between">
        <button
          type="button"
          className="btn bg-gray-500 focus:outline-none hover:bg-gray-700 text-white flex items-center gap-1"
          onClick={() => {
            props.setActionTypes("");
            props.setCenterName("");
          }}
          disabled={props.loadingRequest}
        >
          <MdCancel
            className={`inline-block mr-1 text-xl font-bold ${
              props.loadingRequest ? "animate-pulse" : ""
            }`}
          />
          Cancel
        </button>
        <button
          type="button"
          className="btn btn-blue focus:outline-none flex items-center gap-1"
          onClick={() => props.handleSubmit()}
          disabled={props.loadingRequest}
        >
          <MdSave className="inline-blocktext-xl font-bold" />
          <span>{props.loadingRequest ? "loading..." : "Save"}</span>
        </button>
      </div>
    </Fragment>
  );
};

export default React.memo(CenterForm);
