import React, { Fragment } from "react";
import {
  CentersInterface,
  DistrictInterface,
  RoomLocationInterface,
  RoomsInterface,
} from "../../shared/interfaces";

interface RoomFormProps {
  thisCenter: CentersInterface | null;
  thisDistrict: DistrictInterface | null;
  thisProvince: RoomLocationInterface | null;
  locations: RoomLocationInterface[];
  roomNumber: number;
  roomCapacity: number;
  thisRoom: RoomsInterface | null;
  error: {
    element: string;
    msg: string;
  } | null;
  loadingRequest: boolean;
  setError: (
    value: React.SetStateAction<{
      element: string;
      msg: string;
    } | null>
  ) => void;
  setRoomNumber: React.Dispatch<React.SetStateAction<number>>;
  setRoomCapacity: React.Dispatch<React.SetStateAction<number>>;
  setThisRoom: React.Dispatch<React.SetStateAction<RoomsInterface | null>>;
  setActionTypes: React.Dispatch<
    React.SetStateAction<
      | ""
      | "CREATE_PROVINCE"
      | "UPDATE_PROVINCE"
      | "CREATE_DISTRICT"
      | "UPDATE_DISTRICT"
      | "CREATE_CENTER"
      | "UPDATE_CENTER"
      | "CREATE_ROOM"
      | "UPDATE_ROOM"
    >
  >;
  handleSubmitRoom: () => Promise<void>;
  selectProvince: (province_id: string) => void;
  selectDistrict: (province_id: string, district_id: string) => void;
  selectCenter: (
    province_id: string,
    district_id: string,
    center_id: string
  ) => void;
}

const RoomForm = (props: RoomFormProps) => {
  return (
    <Fragment>
      <div className="py-4 text-base leading-6 space-y-4 text-gray-700 sm:text-lg sm:leading-7">
        {/* PROVINCE SELECT ========================================== */}
        <div className="flex flex-col">
          <label>Province</label>
          <select
            className={`bg-white py-1 px-2 border-2 w-full focus:outline-none focus:bg-gray-100 text-gray-600 focus:text-gray-800 rounded h-10 md:w-full ${
              props.error && props.error.element === "province_id"
                ? "border-red-700"
                : "border-gray-400 focus:border-gray-500"
            }`}
            onChange={(e) => {
              props.error &&
                props.error.element === "province_id" &&
                props.setError(null);
              props.selectProvince(e.target.value);
            }}
            value={props.thisProvince ? props.thisProvince._id : ""}
            disabled={props.loadingRequest}
          >
            <option value="" disabled>
              Select province
            </option>
            {props.locations.map((item, index) => (
              <option value={item._id} key={item._id + index}>
                {item.province_name}
              </option>
            ))}
          </select>
          {props.error && props.error.element === "province_id" && (
            <span className="text-red-700">{props.error.msg}</span>
          )}
        </div>
        {/* END PROVINCE SELECT ========================================== */}

        {/* DISTRICT SELECT ========================================== */}
        <div className="flex flex-col">
          <label>District</label>
          <select
            className={`bg-white py-1 px-2 border-2 w-full focus:outline-none focus:bg-gray-100 text-gray-600 focus:text-gray-800 rounded h-10 md:w-full ${
              props.error && props.error.element === "district_id"
                ? "border-red-700"
                : "border-gray-400 focus:border-gray-500"
            } ${!props.thisProvince && "cursor-not-allowed"}`}
            onChange={(e) => {
              props.error &&
                props.error.element === "district_id" &&
                props.setError(null);
              props.selectDistrict(
                props.thisProvince ? props.thisProvince._id : "",
                e.target.value
              );
            }}
            value={props.thisDistrict ? props.thisDistrict._id : ""}
            disabled={props.loadingRequest || props.thisProvince === null}
          >
            <option value="" disabled>
              Select district
            </option>
            {props.thisProvince !== null &&
            props.locations.filter((loc) => loc._id === props.thisProvince!._id)
              .length > 0 ? (
              props.locations
                .filter((loc) => loc._id === props.thisProvince!._id)[0]
                .districts.map((district, index) => (
                  <option value={district._id} key={index + district._id}>
                    {district.district_name}
                  </option>
                ))
            ) : (
              <option value="" disabled>
                No district found
              </option>
            )}
          </select>
          {props.error && props.error.element === "district_id" && (
            <span className="text-red-700">{props.error.msg}</span>
          )}
        </div>
        {/* END DISTRICT SELECT ========================================== */}

        {/* CENTER SELECT ========================================== */}
        <div className="flex flex-col">
          <label>Center</label>
          <select
            className={`bg-white py-1 px-2 border-2 w-full focus:outline-none focus:bg-gray-100 text-gray-600 focus:text-gray-800 rounded h-10 md:w-full ${
              props.error && props.error.element === "center_id"
                ? "border-red-700"
                : "border-gray-400 focus:border-gray-500"
            } ${!props.thisDistrict && "cursor-not-allowed"}`}
            onChange={(e) => {
              props.error &&
                props.error.element === "center_id" &&
                props.setError(null);
              props.selectCenter(
                props.thisProvince ? props.thisProvince._id : "",
                props.thisDistrict ? props.thisDistrict._id : "",
                e.target.value
              );
            }}
            value={props.thisCenter ? props.thisCenter._id : ""}
            disabled={
              props.loadingRequest ||
              props.thisProvince === null ||
              props.thisDistrict === null
            }
          >
            <option value="" disabled>
              Select center
            </option>
            {props.thisDistrict !== null &&
            props.thisDistrict.centers &&
            props.thisDistrict.centers.length > 0 ? (
              props.thisDistrict.centers.map((center, index) => (
                <option value={center._id} key={index + center._id}>
                  {center.center_name}
                </option>
              ))
            ) : (
              <option value="" disabled>
                No center found
              </option>
            )}
          </select>
          {props.error && props.error.element === "center_id" && (
            <span className="text-red-700">{props.error.msg}</span>
          )}
        </div>
        {/* END CENTER SELECT ========================================== */}

        <div className="flex flex-col">
          <label>Room</label>
          <input
            type="number"
            className={`text-left input-white w-full inline-block border-2 ${
              props.error && props.error.element === "room_number"
                ? "border-red-700"
                : "border-gray-400 focus:border-gray-500"
            }`}
            placeholder="Room number"
            value={props.roomNumber}
            onChange={(e) => props.setRoomNumber(parseInt(e.target.value))}
            disabled={props.loadingRequest}
          />
          {props.error && props.error.element === "room_number" && (
            <span className="text-red-700">{props.error.msg}</span>
          )}
        </div>
        <div className="flex flex-col">
          <label>Seats Capacity</label>
          <input
            type="number"
            className={`text-left input-white w-full inline-block border-2 ${
              props.error && props.error.element === "room_capacity"
                ? "border-red-700"
                : "border-gray-400 focus:border-gray-500"
            }`}
            placeholder="Room capacity"
            value={props.roomCapacity}
            onChange={(e) => props.setRoomCapacity(parseInt(e.target.value))}
            disabled={props.loadingRequest}
          />
          {props.error && props.error.element === "room_capacity" && (
            <span className="text-red-700">{props.error.msg}</span>
          )}
        </div>
      </div>
      <div className="pt-4 flex justify-between">
        <button
          onClick={() => {
            props.setActionTypes("");
            props.setThisRoom(null);
            props.setRoomCapacity(0);
            props.setRoomNumber(0);
          }}
          className={`btn bg-gray-400 text-white hover:bg-gray-600`}
          type="button"
          disabled={props.loadingRequest}
        >
          Exit
        </button>
        <button
          className="btn bg-primary text-white hover:bg-blue-600"
          type="button"
          onClick={() => props.handleSubmitRoom()}
          disabled={props.loadingRequest}
        >
          {props.loadingRequest ? "Loading..." : "Save"}
        </button>
      </div>
    </Fragment>
  );
};

export default React.memo(RoomForm);
