export const toTwoDigit = (value: number): string => {
  var dec = value - Math.floor(value);
  value = value - dec;
  return ("0" + value).slice(-2) + dec.toString().substr(1);
};

export const toAnyDigit = (
  value: string | number,
  padding: number = 3
): string => {
  var zeroes = new Array(padding + 1).join("0");
  return (zeroes + value).slice(-padding);
};

export const capitalizeFirstLetter = (value: string): string => {
  return value.charAt(0).toUpperCase() + value.slice(1);
};

export const isDateExpiried = (paramDate: string) => {
  const thisDate = new Date(paramDate);
  const today = new Date();
  if (thisDate.getTime() < today.getTime()) {
    return true;
  }

  return false;
};
