import React, { Fragment, useEffect, useState } from "react";
import { AiOutlineFilter } from "react-icons/ai";
import { BsCaretRightFill } from "react-icons/bs";
import { connect } from "react-redux";
import { getAllRoom } from "../../actions/location";
import NoResult, { NoResultThemes } from "../../components/NoResult/NoResult";
import { StoreState } from "../../reducers";
import {
  CentersInterface,
  DistrictInterface,
  RoomLocationInterface,
  RoomsInterface,
} from "../../shared/interfaces";
import ViewCenters from "./ViewCenters";

interface AppProps {
  locations: RoomLocationInterface[];
  getAllRoom: Function;
}
const Index: React.FC<AppProps> = (props) => {
  const [actionTypes, setActionTypes] = useState<
    | "CREATE_PROVINCE"
    | "UPDATE_PROVINCE"
    | "CREATE_DISTRICT"
    | "UPDATE_DISTRICT"
    | "CREATE_CENTER"
    | "UPDATE_CENTER"
    | "CREATE_ROOM"
    | "UPDATE_ROOM"
    | ""
  >("");
  const [thisProvince, setThisProvince] =
    useState<RoomLocationInterface | null>(null);
  const [thisDistrict, setThisDistrict] = useState<DistrictInterface | null>(
    null
  );
  const [thisCenter, setThisCenter] = useState<CentersInterface | null>(null);
  const [thisRoom, setThisRoom] = useState<RoomsInterface | null>(null);
  const [loading, setLoading] = useState<boolean>(true);
  // const [districtNotFound, setDistrictNotFound] = useState<boolean>(false);
  const [loadingRequest, setLoadingRequest] = useState<boolean>(false);

  useEffect(() => {
    if (!props.locations.length && loading) {
      props.getAllRoom((status: boolean, msg: string) => {
        setLoading(status);
        if (!status && msg.length > 0) {
          console.error(msg);
        }
        // setLoadingLocations(false);
      });
    } else if (loading) {
      setLoading(false);
    }
  }, [loading, props]);

  if (loading === true) {
    return (
      <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
        Loading locations...
      </div>
    );
  }

  return (
    <Fragment>
      <div className="w-full my-5">
        {/* header title */}
        <div className="flex justify-between w-full p-2 gap-1">
          <div className="flex flex-col">
            <span className="text-blue-600 text-2xl md:text-2xl lg:text-3xl font-bold">
              Locations Management
            </span>

            <div className="flex flex-row items-center md:gap-3 gap-2 mt-1">
              <AiOutlineFilter className="text-xl" />
              <div className="flex flex-row items-center gap-1">
                {thisProvince && (
                  <span className="text-gray-700 text-base font-bold">
                    {thisProvince.province_name}
                  </span>
                )}
                {thisDistrict && (
                  <div className="flex flex-row items-center gap-1">
                    <BsCaretRightFill />
                    <span className="text-blue-600 text-base font-bold underline">
                      {thisDistrict.district_name} district
                    </span>
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
        {/* end header title */}

        {props.locations.length > 0 ? (
          <div className="w-full">
            {/* ============================= CENTERS ============================= */}
            <ViewCenters
              locations={props.locations}
              thisProvince={thisProvince}
              thisProvinceId={thisProvince ? thisProvince._id : ""}
              thisDistrict={thisDistrict}
              thisCenter={thisCenter}
              thisRoom={thisRoom}
              loadingRequest={loadingRequest}
              actionTypes={actionTypes}
              setThisProvince={setThisProvince}
              setThisDistrict={setThisDistrict}
              setLoadingRequest={setLoadingRequest}
              setThisCenter={setThisCenter}
              setThisRoom={setThisRoom}
              setActionTypes={setActionTypes}
            />
            {/* =========================== END CENTERS =========================== */}
          </div>
        ) : (
          <NoResult theme={NoResultThemes.Dark} value="No Locations found!" />
        )}
      </div>
    </Fragment>
  );
};

const mapStateToProps = ({
  locations,
}: StoreState): {
  locations: RoomLocationInterface[];
} => {
  return { locations: locations.roomLocation };
};

export const Centers = connect(mapStateToProps, {
  getAllRoom,
})(Index);
